        </div>
        <!-- 图片 -->
        <div class="cart_img_wrap">
            <img :src="product.goods_image" alt="">
        </div>
        <!-- 商品信息 -->
        <div class="cart_info_wrap">
            <div class="goods_name">{{product.goods_name}}</div>
            <div class="gods_price_wrap">
                <div class="goods_price">￥{{product.goods_price}}</div>
                <div class="cart_num_tool">
                    <div 
                    class="good_edit"
                    >-</div>
                    <div class="good_num">{{product.count}}</div>
                    <div 
                    class="good_edit"
                    >+</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
//import x from ''
import CheckButton from 'components/content/checkButton/CheckButton'
export default {
    name: 'CartListItem',
    props: {
        product:{
