            default(){
                return {}
            }
        }
    },
    data () {
        return {
        }
    },
    components: {
        CheckButton
    },
    computed: {},
    watch: {},
    methods: {
        handleCleck(){
            // 本地修改(不好)
            // this.product.checked = !this.product.checked
            // Vuex修改
            let goods_id = this.product.goods_id
            this.$store.dispatch('checkedChange', goods_id)
        }
    },
    created() {
    },
    beforeMount () { },
    mounted () { 
        this.clecked = this.product.clecked
    },
    activated () { }
}
</script>
