<template>
<!-- 底部工具栏 -->
    <div class="footer_tool">
        <!-- 全选 -->
        <div class="all_chk_wrap">
            <check-button :is-clecked="isSelectAll" @click.native="handleCleck" />全选
        </div>
        <!-- 总价格 -->
        <div class="total_price_wrap">
            <div class="total_price">
                合计：<span class="total_price_span">￥{{totalPrice}}</span>
            </div>
            <div>包含运费</div>
        </div>
        <!-- 结算 -->
        <div class="order_pay_wrap" @click="payClick">
            结算({{checkLength}})
        </div>
    </div>
</template>

<script>
    //import x from ''
    import CheckButton from 'components/content/checkButton/CheckButton'
    import { mapGetters } from 'vuex'
    export default {
        name: 'CartBottom',
        props: {},
        data() {
            return {
                cleckedAll: true
            }
        },
        components: {
            CheckButton
        },
        computed: {
            ...mapGetters([
                'cartList'
            ]),
            totalPrice (){
                // console.log(this.cartList);
                return this.cartList.filter(item => {
                    return item.checked
                }).reduce((preValue,item) => {
                    return item.goods_price * item.count
                },0) 
            },
            checkLength() {
                return this.cartList.filter(item => {
                    return item.checked
                }).length
            },
            isSelectAll() {
                if (this.cartList.length === 0) {
                    return false
                }else{
                    return !this.cartList.find(item => !item.checked)
                }
            }

        },
        watch: {},
        methods: {
            handleCleck(){
                if (this.isSelectAll) {
                    this.cartList.forEach(item => {item.checked = !this.isSelectAll });
                }else{
                    this.cartList.forEach(item => { item.checked = true });
                }
            },
            payClick() {
                if (!this.checkLength) {
                    this.$toast.show('您还没有选择商品',1500)
                }else{
                    this.$toast.show('下单成功', 1500)
                }
            }
        },
        beforeMount() { },
        mounted() { },
        activated() { }
    }
</script>

<style scoped>
    .bootom_bar{
        box-sizing: border-box;
        height: 40px;
        position: fixed;
        right: 0;
        left: 0;
        bottom: 62px;
        background-color: blue;
    }
.footer_tool{
  /* 置底 */
    position: fixed;
    bottom: 62px;
    left: 0;
    width: 100%;
    height: 40px;
    background-color: #fff;
    display: flex;
    border-top: 1px solid #ccc;
}
    .all_chk_wrap{
        flex: 2;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .total_price_wrap{
        flex: 4;
        text-align: right;
        padding-right: 5px;
    }
            .total_price_text{
                color: var(--color-tint);
                font-size: 16px;  
            }
    .order_pay_wrap{
        flex: 3;
        background-color: var(--color-tint);
        color: #fff;
        font-size: 16px;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>